          </div>
          <div
            class="up_item btn_convenios"
            :class="btnClassConvenios"
            @click="setBoardContent(1)"
          >
            <span style="margin-right: 25%;">Convenios Isapres</span>
          </div>
          <div
            class="up_item btn_empresas"
            :class="btnClassEmpresas"
