      //       this.dialogData.texto = 'Esta es la información de Convenio Empresas'
      //       this.dialogData.active = true
      //       break
      //   }
      // },
      setBoardContent (seccion) {
        this.btnClassConvenios = 'btn-inactive'
        this.btnClassAranceles = 'btn-inactive'
        this.btnClassEmpresas = 'btn-inactive'

        switch (seccion) {
          case 1:
            this.boardContent =
